import * as React from "react";
import { LetsRandOptions, parseDist, parseSize } from "./LetsRandOptions";
import { generateStrings } from "./GenerateStrings";
import figlet from "figlet";
import { isMobile } from "react-device-detect";

interface PageState extends LetsRandOptions {
  sizeString: string;
  strings: string[];
}

const kControlStyle: React.CSSProperties = {
  fontSize: isMobile ? "1.5rem" : undefined,
};

const kCheckBoxStyle: React.CSSProperties = {
  transform: isMobile ? "scale(1.5)" : undefined,
};

export class Page extends React.Component<{}, PageState> {
  constructor(props: {}) {
    super(props);
    this.state = {
      distribution: "natural",
      count: 10,
      size: [4, 6],
      useFiglet: false,
      useLower: false,
      sizeString: "",
      strings: [],
    };

    this.onChangeDistribution = this.onChangeDistribution.bind(this);
    this.onChangeCount = this.onChangeCount.bind(this);
    this.onChangeSize = this.onChangeSize.bind(this);
    this.onChangeLower = this.onChangeLower.bind(this);
    this.onChangeFiglet = this.onChangeFiglet.bind(this);

    this.generate = this.generate.bind(this);
  }

  componentDidMount() {
    this.generate();
  }

  onChangeDistribution(event: React.ChangeEvent<HTMLSelectElement>) {
    const value = parseDist(event.target.value);
    this.setState({ distribution: value }, this.generate);
  }

  onChangeCount(event: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ count: parseInt(event.target.value) }, this.generate);
  }

  onChangeSize(event: React.ChangeEvent<HTMLInputElement>) {
    try {
      const size = parseSize(event.target.value);
      this.setState({ size: size, sizeString: "" }, this.generate);
    } catch (err) {
      this.setState({ sizeString: event.target.value });
    }
  }

  onChangeLower() {
    this.setState({ useLower: !this.state.useLower }, this.generate);
  }

  onChangeFiglet() {
    this.setState({ useFiglet: !this.state.useFiglet }, this.generate);
  }

  sizeString() {
    if (this.state.sizeString.length) {
      return this.state.sizeString;
    } else if (typeof this.state.size === "number") {
      return this.state.size.toString();
    } else {
      return `${this.state.size[0]}-${this.state.size[1]}`;
    }
  }

  figletText(input: string): Promise<string> {
    return new Promise((resolve, reject) => {
      figlet.text(
        input,
        { font: "Small", horizontalLayout: "universal smushing" },
        (err, res) => {
          if (err) {
            reject(err);
          } else {
            resolve(res);
          }
        }
      );
    });
  }

  async generate() {
    const strings = await generateStrings(this.state);
    if (this.state.useFiglet) {
      const figged: string[] = [];
      for (const str of strings) {
        figged.push(str);
        figged.push(await this.figletText(str));
      }
      this.setState({ strings: figged });
    } else {
      this.setState({ strings: strings });
    }
  }

  render() {
    return (
      <div style={{ display: "grid", gridTemplateColumns: "1fr 4fr" }}>
        <h1>LetsRand</h1>
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "8em 5em",
            minWidth: "15em",
            gridRowGap: isMobile ? "1em" : "0.5em",
            gridColumnStart: 1,
          }}
        >
          <h2 style={{ gridColumnStart: 1, gridColumnEnd: 3 }}>Options</h2>
          <div>Distribution:</div>
          <select
            value={this.state.distribution}
            onChange={this.onChangeDistribution}
            style={kControlStyle}
          >
            <option value="natural">Natural</option>
            <option value="even">Even</option>
            <option value="bigram">Bigram</option>
          </select>

          <div>Count:</div>
          <input
            type="number"
            min={1}
            max={100}
            value={this.state.count}
            onChange={this.onChangeCount}
            style={kControlStyle}
          />

          <div>Size:</div>
          <input
            style={{
              fontSize: kControlStyle.fontSize,
              color: this.state.sizeString.length ? "red" : undefined,
            }}
            type="text"
            value={this.sizeString()}
            onChange={this.onChangeSize}
          />

          <label htmlFor="useLower">Lowercase:</label>
          <input
            style={kCheckBoxStyle}
            id="useLower"
            type="checkbox"
            checked={this.state.useLower}
            onChange={this.onChangeLower}
          />

          <label htmlFor="useFiglet">Figlet:</label>
          <input
            style={kCheckBoxStyle}
            id="useFiglet"
            type="checkbox"
            checked={this.state.useFiglet}
            onChange={this.onChangeFiglet}
          />

          <button
            onClick={this.generate}
            style={{
              fontSize: kControlStyle.fontSize,
              margin: "1em 3em",
              gridColumnStart: 1,
              gridColumnEnd: 3,
            }}
          >
            More...
          </button>
        </div>
        <div style={{ gridArea: "1/2/4/3" }}>
          <pre>{this.state.strings.join("\n")}</pre>
        </div>
      </div>
    );
  }
}
