interface DistributionEntry {
  letter: string;
  count: number;
}

const kLetterCountTable : DistributionEntry[] = [
  { letter: "E", count: 21912 },
  { letter: "T", count: 16587 },
  { letter: "A", count: 14810 },
  { letter: "O", count: 14003 },
  { letter: "I", count: 13318 },
  { letter: "N", count: 12666 },
  { letter: "S", count: 11450 },
  { letter: "R", count: 10977 },
  { letter: "H", count: 10795 },
  { letter: "D", count: 7874 },
  { letter: "L", count: 7253 },
  { letter: "U", count: 5246 },
  { letter: "C", count: 4943 },
  { letter: "M", count: 4761 },
  { letter: "F", count: 4200 },
  { letter: "Y", count: 3853 },
  { letter: "W", count: 3819 },
  { letter: "G", count: 3693 },
  { letter: "P", count: 3316 },
  { letter: "B", count: 2715 },
  { letter: "V", count: 2019 },
  { letter: "K", count: 1257 },
  { letter: "X", count: 315 },
  { letter: "Q", count: 205 },
  { letter: "J", count: 188 },
  { letter: "Z", count: 128 },
];

const kMaxDistributionCount = kLetterCountTable
  .map((el) => el.count)
  .reduce((accum, el) => accum + el);


export class NaturalDistribution extends Distribution {
  async next(): Promise<string> {
    for (;;) {
      let value = await this.nextRandom() * kMaxDistributionCount;
      for (const entry of kLetterCountTable) {
        value -= entry.count;
        if (value <= 0) {
          return entry.letter;
        }
      }
    }
  }
}
