export interface LetsRandOptions {
  distribution: 'natural' | 'even' | 'bigram';
  count: number;
  size: number | [number, number];
  useLower: boolean;
  useFiglet: boolean;
}

export function parseSize(input: string | undefined): number | [number, number] {
  if (input === undefined || input === null) {
    return 6;
  }

  if (input.match(/^[1-9][0-9]*$/)) {
    return parseInt(input);
  } else if (input.match(/^[1-9][0-9]*-[1-9][0-9]*$/)) {
    const parts = input.split("-");
    return [parseInt(parts[0]), parseInt(parts[1])];
  } else {
    throw new Error(
      "Size must be a positive integer or a range between two positive integers."
    );
  }
}

export function parseDist(input: string) : 'even' | 'natural' | 'bigram' {
  if (input === 'even' || input === 'natural' || input === 'bigram') {
    return input
  }
  throw new Error("Distribution must be one of 'even | natural | bigram'")
}

