import { EvenDistribution } from "./EvenDistribution";
import { NaturalDistribution } from "./NaturalDistribution";
import { BigramDistribution } from "./BigramDistribution";
import { LetsRandOptions } from "./LetsRandOptions";

export async function generateStrings({
  distribution,
  count,
  size,
  useLower, }: LetsRandOptions) {
  const result: string[] = [];
  const generator = distribution === "natural"
    ? new NaturalDistribution()
    : distribution === "even"
      ? new EvenDistribution()
      : new BigramDistribution();

  if (typeof size === "number") {
    size = [size, size];
  }

  const [lenMin, lenMax] = size;
  const range = 1 + lenMax - lenMin;

  for (let i = 0; i < count; i++) {
    const chars: string[] = [];
    const len = Math.floor(await generator.nextRandom() * range) + lenMin;
    generator.reset();
    for (let j = 0; j < len; j++) {
      chars.push(await generator.next());
    }
    const word = chars.join("");
    result.push(useLower ? word.toLowerCase() : word);
  }
  return result;
}
