export abstract class Distribution {
  private pool = new Uint32Array(16)
  private offset = 16;

  abstract next(): Promise<string>;

  reset(): void {};

  async nextRandom(): Promise<number> {
    if (this.offset >= this.pool.length) {
      this.offset = 0;
      if (typeof window === 'undefined') {
        const crypto = await import('crypto');
        crypto.randomFillSync(this.pool);
      } else {
        crypto.getRandomValues(this.pool);
      }
    }
    return this.pool[this.offset++] / 0x1_0000_0000;
  }
}
